.Container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.inner {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

img {
  max-width: 350px;
}

.NumbersContainer {
  width: 100%;
  font-size: 14px;
  display: flex;
  justify-content: space-around;
}
.NumbersContainer > div {
  text-align: center;
  flex-wrap: wrap;
  padding: 10px;
  color: #8585ad;
}
